/* components/ReviewReminderSection.css */
.review-reminder-section {
  padding: 20px 0;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
}

.review-reminder-section .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/*  */
.user-welcome-banner {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 30px;
  border-radius: 15px;
  margin-bottom: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.user-welcome-banner h3 {
  margin: 0 0 10px 0;
  font-size: 1.8em;
  font-weight: 600;
}

.user-welcome-banner p {
  margin: 0;
  opacity: 0.9;
  font-size: 1.1em;
}

/*  */
.review-header {
  display: grid;
  grid-template-columns: 1fr 300px;
  gap: 30px;
  margin-bottom: 30px;
}

.review-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
}

.stat-card {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.stat-card.urgent {
  border-left: 4px solid #ff6b6b;
}

.stat-card.medium {
  border-left: 4px solid #ffa726;
}

.stat-card.low {
  border-left: 4px solid #4ecdc4;
}

.stat-card.total {
  border-left: 4px solid #667eea;
}

.stat-icon {
  font-size: 2em;
  margin-right: 15px;
}

.stat-content {
  flex: 1;
}

.stat-number {
  font-size: 1.8em;
  font-weight: 700;
  display: block;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9em;
  color: #666;
  font-weight: 500;
}

.stat-description {
  font-size: 0.8em;
  color: #999;
  margin-top: 2px;
}

/*  */
.review-progress {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.progress-header span {
  font-weight: 600;
  color: #333;
}

.progress-bar {
  height: 8px;
  background: #e9ecef;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 10px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #4ecdc4, #667eea);
  border-radius: 10px;
  transition: width 0.5s ease;
}

.progress-stats {
  text-align: center;
  font-size: 0.9em;
  color: #666;
}

/*  */
.review-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

.controls-left {
  display: flex;
  align-items: center;
  gap: 20px;
}

.search-box {
  position: relative;
  display: flex;
  align-items: center;
}

.search-icon {
  position: absolute;
  left: 12px;
  font-size: 1.1em;
  color: #666;
}

.search-input {
  padding: 10px 15px 10px 40px;
  border: 2px solid #e9ecef;
  border-radius: 25px;
  font-size: 0.95em;
  width: 250px;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.urgency-filter {
  display: flex;
  gap: 8px;
}

.urgency-btn {
  padding: 8px 16px;
  border: 2px solid #e9ecef;
  background: white;
  border-radius: 20px;
  font-size: 0.9em;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.urgency-btn:hover {
  border-color: #667eea;
  transform: translateY(-2px);
}

.urgency-btn.active {
  border-color: #667eea;
  background: #667eea;
  color: white;
}

.urgency-btn.high.active {
  border-color: #ff6b6b;
  background: #ff6b6b;
}

.urgency-btn.medium.active {
  border-color: #ffa726;
  background: #ffa726;
}

.urgency-btn.low.active {
  border-color: #4ecdc4;
  background: #4ecdc4;
}

.settings-btn {
  padding: 10px 20px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 25px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.settings-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

/*  */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  
}

.modal-content {
  background: white;
  border-radius: 15px;
  width: 90%;
  max-width: 700px;
  max-height: 100vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 30px;
  border-bottom: 1px solid #e9ecef;
}

.modal-header h3 {
  margin: 0;
  color: #333;
  font-weight: 600;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  color: #666;
  padding: 5px;
  border-radius: 50%;
  transition: background-color 0.3s ease;
}

.close-btn:hover {
  background: #f8f9fa;
}

.settings-content {
  padding: 30px;
}

.setting-item {
  margin-bottom: 30px;
}

.setting-item label {
  display: block;
  margin-bottom: 15px;
  font-weight: 600;
  color: #333;
}

.hint {
  font-weight: normal;
  color: #666;
  font-size: 0.9em;
  margin-left: 5px;
}

.threshold-control {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
}

.threshold-slider {
  flex: 1;
  height: 6px;
  border-radius: 3px;
  background: #e9ecef;
  outline: none;
  -webkit-appearance: none;
}

.threshold-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #667eea;
  cursor: pointer;
  border: 3px solid white;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.threshold-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #667eea;
  cursor: pointer;
  border: 3px solid white;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.threshold-value {
  font-weight: 600;
  color: #667eea;
  min-width: 60px;
}

.threshold-presets {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.preset-btn {
  padding: 8px 16px;
  border: 2px solid #e9ecef;
  background: white;
  border-radius: 20px;
  font-size: 0.9em;
  cursor: pointer;
  transition: all 0.3s ease;
}

.preset-btn:hover {
  border-color: #667eea;
}

.preset-btn.active {
  border-color: #667eea;
  background: #667eea;
  color: white;
}

/*  */
.urgency-explanation {
  margin-bottom: 25px;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 10px;
}

.urgency-explanation h4 {
  margin: 0 0 15px 0;
  color: #333;
}

.urgency-levels {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.urgency-level {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 12px;
  background: white;
  border-radius: 8px;
  border-left: 4px solid transparent;
}

.urgency-level.high {
  border-left-color: #ff6b6b;
}

.urgency-level.medium {
  border-left-color: #ffa726;
}

.urgency-level.low {
  border-left-color: #4ecdc4;
}

.urgency-color {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  flex-shrink: 0;
}

.urgency-info {
  display: flex;
  flex-direction: column;
}

.urgency-info strong {
  color: #333;
  margin-bottom: 2px;
}

.urgency-info span {
  font-size: 0.85em;
  color: #666;
}

.setting-info {
  background: #fff9e6;
  padding: 20px;
  border-radius: 10px;
  border-left: 4px solid #ffa726;
}

.setting-info h4 {
  margin: 0 0 15px 0;
  color: #333;
}

.setting-info ul {
  margin: 0;
  padding-left: 20px;
}

.setting-info li {
  margin-bottom: 8px;
  color: #666;
  line-height: 1.4;
}

.setting-info li:last-child {
  margin-bottom: 0;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  padding: 20px 30px;
  border-top: 1px solid #e9ecef;
}

.cancel-btn, .confirm-btn {
  padding: 10px 25px;
  border: none;
  border-radius: 25px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cancel-btn {
  background: #f8f9fa;
  color: #666;
}

.cancel-btn:hover {
  background: #e9ecef;
}

.confirm-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.confirm-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

/*  */
.review-questions {
  margin-top: 20px;
}

.empty-review-state {
  text-align: center;
  padding: 60px 20px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

.empty-icon {
  font-size: 4em;
  margin-bottom: 20px;
}

.empty-review-state h3 {
  margin: 0 0 15px 0;
  color: #333;
  font-weight: 600;
}

.empty-review-state p {
  margin: 0 0 25px 0;
  color: #666;
  font-size: 1.1em;
}

.adjust-settings-btn {
  padding: 12px 30px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 25px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.adjust-settings-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.questions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 20px;
}

/*  */
.review-question-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
}

.review-question-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.review-question-card.updating {
  opacity: 0.7;
  pointer-events: none;
}

.card-header {
  padding: 20px 20px 15px;
  border-bottom: 1px solid #f0f0f0;
  position: relative;
}

.urgency-indicator {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
}

.question-meta {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  flex-wrap: wrap;
}

.urgency-badge {
  padding: 4px 12px;
  border-radius: 15px;
  color: white;
  font-size: 0.8em;
  font-weight: 500;
}

.category-tag {
  padding: 4px 12px;
  background: #f8f9fa;
  border-radius: 15px;
  font-size: 0.8em;
  color: #666;
}

.time-info {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.last-reviewed, .days-ago {
  font-size: 0.85em;
  color: #666;
}

.urgency-description {
  font-size: 0.8em;
  color: #999;
  font-style: italic;
}

.card-content {
  padding: 20px;
}

.question-title {
  margin: 0 0 15px 0;
  font-size: 1.1em;
  font-weight: 600;
  color: #333;
  line-height: 1.4;
}

.answer-preview {
  color: #666;
  font-size: 0.9em;
  line-height: 1.5;
  margin-bottom: 15px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.question-tags {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.difficulty-tag {
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.8em;
  font-weight: 500;
}

.difficulty-easy {
  background: #f6ffed;
  color: #52c41a;
  border: 1px solid #b7eb8f;
}

.difficulty-medium {
  background: #fffbe6;
  color: #faad14;
  border: 1px solid #ffe58f;
}

.difficulty-hard {
  background: #fff2f0;
  color: #f5222d;
  border: 1px solid #ffa39e;
}

.tag {
  padding: 4px 10px;
  background: #f0f2f5;
  border-radius: 12px;
  font-size: 0.8em;
  color: #666;
}

.card-footer {
  padding: 15px 20px;
  background: #fafafa;
  border-top: 1px solid #f0f0f0;
}

.review-actions {
  display: flex;
  gap: 10px;
}

.review-now-btn, .postpone-btn {
  flex: 1;
  padding: 10px 15px;
  border: none;
  border-radius: 8px;
  font-size: 0.9em;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.review-now-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.review-now-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.review-now-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.postpone-btn {
  background: #f0f2f5;
  color: #666;
}

.postpone-btn:hover:not(:disabled) {
  background: #e4e6e9;
  transform: translateY(-2px);
}

.postpone-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/*  */
.updating-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.updating-spinner {
  width: 30px;
  height: 30px;
  border: 3px solid #f3f3f3;
  border-top: 3px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/*  */
.auth-required-container {
  text-align: center;
  padding: 60px 20px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

.auth-required-icon {
  font-size: 4em;
  margin-bottom: 20px;
}

.auth-required-container h2 {
  margin: 0 0 15px 0;
  color: #333;
  font-weight: 600;
}

.auth-required-container p {
  margin: 0 0 25px 0;
  color: #666;
  font-size: 1.1em;
}

.auth-required-actions {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.login-btn, .btn-back {
  padding: 12px 30px;
  border: none;
  border-radius: 25px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.btn-back {
  background: #f8f9fa;
  color: #666;
}

.btn-back:hover {
  background: #e9ecef;
}

/*  */
@media (max-width: 1024px) {
  .review-header {
    grid-template-columns: 1fr;
  }
  
  .review-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .questions-grid {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

@media (max-width: 768px) {
  .review-controls {
    flex-direction: column;
    gap: 20px;
  }
  
  .controls-left {
    flex-direction: column;
    width: 100%;
  }
  
  .search-input {
    width: 100%;
  }
  
  .urgency-filter {
    width: 100%;
    justify-content: center;
  }
  
  .review-stats {
    grid-template-columns: 1fr;
  }
  
  .questions-grid {
    grid-template-columns: 1fr;
  }
  
  .review-actions {
    flex-direction: column;
  }
  
  .auth-required-actions {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 15px;
  }
  
  .user-welcome-banner {
    padding: 20px;
  }
  
  .modal-content {
    width: 95%;
    margin: 20px;
  }
  
  .settings-content {
    padding: 20px;
  }
  
  .threshold-presets {
    justify-content: center;
  }
}

